@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
.Main {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border: 1px solid black;
    width: 80%;
    height: 40px;
    border-radius: 20px;
    background-color: rgba(73, 89, 90, 0.479);
}
.Smain {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 576px) {
    .Main,
    .Npre,
    .Nnext {
        height: 25px;

        display: flex;
        align-items: center;

        box-sizing: border-box;
    }
    .Main{
        width: 90%;
    }
}
.NPbtn {
    -webkit-transform: translate(-5%);
            transform: translate(-5%);
}

:root {
    --fontColor: rgb(0, 207, 207);
}
.card {
    
    display: flex;
    flex-direction: column;
    text-align: center;
    border: none;
    cursor: pointer;
    margin: 10px;
    box-sizing: border-box;
    background-color: transparent;
    border-radius: 14px;
}
.imgCrd {
    position: relative;
    text-decoration: none;
}
.imgCrd > span {
    position: absolute;
    left: 5%;
    bottom: -5%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgb(0, 0, 0);
    text-align: center;
    border: 3px solid rgb(77, 77, 77);
    line-height: 95%;
    /* letter-spacing: px; */
    font-size: 13px;
    font-weight: bold;
    color: rgb(255, 255, 255);

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.imgCrd > img {
    width: 150px;
    height: 230px;
    border-radius: 13px;
}
@media screen and (max-width: 576px) {
    .imgCrd > img {
        width: 100px;
        height: 170px;
    }
    .cName {
        width: 100px;
        text-decoration: none;
    }
    .imgCrd > span {
        position: absolute;
        bottom: -2%;

        -webkit-transform: translate(-50%);

                transform: translate(-50%);
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: rgb(0, 0, 0);
        text-align: center;

        line-height: 95%;
        /* letter-spacing: px; */
        font-size: 10px;
    }
}
.cName {
    font-family: sans-serif;
    letter-spacing: -1px;
    font-weight: bold;
    margin: 5px 0px;
    font-size: 16px;
    max-width: 150px;
    max-height: 20px;
    overflow: hidden;
}
.cName:hover {
    overflow: visible;
    transition: 0.1s;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    color: rgb(0, 207, 207);
    color: var(--fontColor);
}
.cDate {
    font-family: serif;
    font-weight: bold;
    font-size: 13px;
    /* letter-spacing: -1px; */
    text-transform: capitalize;
    color: rgb(92, 92, 92);
}

.movies{
    background-color: rgb(0, 191, 255);
    
}

.mainHeader{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    color: white;
}
.searchbar{
    border-radius: 13px;
    width: 50%;

}
heading1,heading2{
    text-align: center;
}
input{
    width: 80%;
    border-radius: 5px;
    outline: none;
    border: none;
    text-align: center;
    caret-color: rgb(51, 51, 51);
}
@media screen and (max-width: 576px) {
    .searchbar{
        width: 95%;
    }
    input{
        width: 90%;
    }
    
}
* {
    padding: 0;
    margin: 0;
    font-family: "Merriweather", serif;
}

.item-container {
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.MovMainImg {
    position: relative;
    box-shadow: 2px 5px 1vh white;
    border-radius: 13px;
    margin: 2rem;
}
.img{
    width: 300px;
    height: 430px;
    border-radius: 13px;
}
@media screen and (max-width: 576px) {
    .item-container {
        flex-direction: column;
        text-align: center;
    }
    .MovMainImg {
        width: 300px;
        height: 430px;
        margin: 3% 0%;
    }
}
.image {
    position: relative;
}
.rating {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 5px solid rgb(163, 163, 163);
    position: absolute;
    bottom: -4%;
    right: -1%;
    background: black;
}
.rating>span{
    font-weight: bold;
}
